<template>
 <div class="sportManagement">
   <div class="tabBtn textLeft">
     <el-button type="primary">批量导入</el-button>
     <el-button type="primary" @click="batchLoad()">批量下载</el-button>
     <el-button type="primary" @click="batchDelete()">批量删除成绩</el-button>
   </div>
   <div class="textLeft">
     <h4 class="yearTime">
       {{yearTime}}
     </h4>
   </div>
   <div class="selectWrap textLeft">
     <h4>
        请选择试卷名称:
     </h4>
     <el-select v-model="value" @change="querySelect(value)" placeholder="请选择">
       <el-option
         v-for="item in datas"
         :key="item.id"
         :label="item.examName"
         :value="item.examId">
       </el-option>
     </el-select>
   </div>
   <div class="gradeWrap textLeft">
     <span>年级：</span>
     <ul v-for="(gradeList, index) in datas" v-if="gradeList.examId == value">

       <li @click="queryTableMsg($event,grades.classes,index)" ref="li" :class="{active:active==index}" v-for="(grades,index) in gradeList.grades">
         <span hidden>{{grades.classes}}</span>
         {{grades.gradeName}}
       </li>
     </ul>
   </div>
   <div class="tableWrap">
     <el-table
       :data="tableData3"
       border
       tooltip-effect="dark"
       style="width: 100%"
       @select-all="allSelectionChange"
       @select="handleSelectionChange">
       <el-table-column
         type="selection"
         width="85">
         <!--<template scope="scope">{{ scope.row.index }}</template>-->
       </el-table-column>
       <el-table-column
         prop="gradeName"
         label="年级"
         width="120">
       </el-table-column>
       <el-table-column
         prop="className"
         label="班级"
         width="120">
       </el-table-column>
       <el-table-column
         prop="term"
         label="学期"
         width="120">
       </el-table-column>
       <el-table-column
         label="创建时间"
         width="200">
         <template scope="scope">{{ scope.row.createTime }}</template>
       </el-table-column>
       <!--<el-table-column-->
         <!--prop="name"-->
         <!--label="姓名"-->
         <!--width="120">-->
       <!--</el-table-column>-->
       <!--<el-table-column-->
         <!--prop="address"-->
         <!--label="地址"-->
         <!--show-overflow-tooltip>-->
       <!--</el-table-column>-->
       <el-table-column label="操作">
         <template scope="scope">
           <el-button
             size="small"
             @click="handleQuery(scope.$index, scope.row)" >查看</el-button>
           <el-button
             size="small"
             @click="handleEdit(scope.$index, scope.row)">下载
           </el-button>
           <el-button
             size="small"
             type="danger"
             @click="handleDelete(scope.$index, scope.row)">删除</el-button>
         </template>
       </el-table-column>
     </el-table>
   </div>
 </div>
</template>

<script>
  export default {
    name: 'sportManagement',
    data() {
      return {
        tableData3: [],
        allSelectMsg:[],
        multipleSelection: [],
        yearTime:"2017-2018学年",
        datas: [],
        gradeList:[],
        value: '',
        active:"0",
        param: {},
        tableMsg:[],
        grades:{
          scoreList:[]
        }
      }
    },
//    beforeMount: function () {
    mounted: function () {
      let $this = this;
      this.$ajax('physical/index?schoolId=1',
//      this.$ajax('static/score.json',
//        $this.param
      )
        .then(function (response) {
//          console.log(response);
          $this.tableData3 = response.data.datas[0].grades[0].classes;
          $this.value = response.data.datas[0].examId;
          $this.datas = response.data.datas;
          console.log($this.datas);

          $this.yearTime = response.data.schoolYear;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    methods: {
      querySelect(value){
//        console.log(this.$refs.li[0].firstChild.innerHTML);
        this.value = value;
        this.active = "0";
        this.tableData3 = eval(this.$refs.li[0].firstChild.innerHTML);
      },
      queryTableMsg(event,data,index){
        this.tableData3 = data;
        this.active = index;
      },
      allSelectionChange(val){
//          console.log(val);
        this.multipleSelection = [];
        this.allSelectMsg = val;
//        console.log(this.allSelectMsg)
      },
      handleSelectionChange(val) {
          console.log(val);

        this.allSelectMsg = [];
        this.multipleSelection = val;
//        console.log(this.multipleSelection);
//        console.log("all"+this.allSelectMsg);

      },
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleQuery(index, row) {
//        this.router.push({ path: '/querySubjectScore' });
//        this.$route.push( { path: '/querySubjectScore/:id'});
//        this.$route.path = "/querySubjectScore"
//        location.href = '/querySubjectScore?data=1';
        this.$router.push({ path: 'querySportScore',  query: {"examId":this.value,"classId":row.classId}});

//
        console.log(index, row);
//        console.log(this,this.$route.path);
      },
      handleDelete(index, row) {
          let $this = this;
//        console.log(index, row);
//        this.tableData3.splice(this.nowIndex, 1, newValue);
//        this.tableData3.splice(index, 1);
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$ajax('physical/delClassScore',
            {"classId" : row.classId,"examId":$this.value}
          ).then(function (response) {
            if (response.code == 200){
              $this.tableData3.splice(index, 1);
              $this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              $this.$message({
                type: 'info',
                message: '删除失败！'
              });
            }
          }).catch(function (error) {
              console.log(error);
            });
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      batchLoad(){
        if (this.allSelectMsg && this.allSelectMsg.length > 0){
          let allSelectMsg = this.allSelectMsg;
          let downLoadId = [];
          allSelectMsg.forEach( ( item, i ) => {
              downLoadId[i] = item.id;
            });
          console.log(downLoadId);
          this.$confirm('您将批量下载选中的文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$ajax('static/scores.json',
//              {
//                "downLoadId":downLoadId
//              }
            ).then(function (response) {
              if (response.code == 200){
                alert("下载成功！")
              }else {
                $this.$message({
                  type: 'info',
                  message: '下载失败！'
                });
              }
            }).catch(function (error) {
              console.log(error);
            });
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });

        }else if (this.multipleSelection && this.multipleSelection.length >0){
          let multipleSelection = this.multipleSelection;
          let downLoadId = [];
          multipleSelection.forEach( ( item, i ) => {
            downLoadId[i] = item.id;
          });
          console.log(downLoadId);
          this.$confirm('您将批量下载选中的文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$ajax('static/scores.json',
//              {
//                "downLoadId":downLoadId
//              }
            ).then(function (response) {
              if (response.code == 200){
                alert("下载成功！")
              }else {
                $this.$message({
                  type: 'info',
                  message: '下载失败！'
                });
              }
            }).catch(function (error) {
              console.log(error);
            });
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }else {
          alert("未选中文件！")
        }
      },
      batchDelete(){
          let $this = this;
          if (this.allSelectMsg && this.allSelectMsg.length > 0){
            let allSelectMsg = this.allSelectMsg;
            let delId = [];
            allSelectMsg.forEach( ( item, i ) => {
              delId[i] = item.classId;
            });
//            console.log(delId);
            delId = delId.join();
            this.$confirm('您将批量删除选中的文件, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.$ajax('static/scores.json',
              {"classId" : delId,"examId":$this.value}
              ).then(function (response) {
                if (response.code == 200){
                  $this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                  $this.tableData3 = [];
                  $this.allSelectMsg = [];
                  $this.multipleSelection = [];

                }else {
                  $this.$message({
                    type: 'info',
                    message: '删除失败！'
                  });
                }
              }).catch(function (error) {
                console.log(error);
              });
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });
            });
          }else if (this.multipleSelection && this.multipleSelection.length >0){
            let multipleSelection = this.multipleSelection;
            let delId = [];
            multipleSelection.forEach( ( item, i ) => {
              delId[i] = item.classId;
            });
//            console.log(delId);
            delId = delId.join();
            this.$confirm('您将批量删除选中的文件, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.$ajax('physical/delClassScore',
                {"classId" : delId,"examId":$this.value}
              ).then(function (response) {
                if (response.code == 200){
                  $this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                  multipleSelection.forEach( ( item, i ) => {
//                    console.log(item);
                      $this.tableData3.forEach( ( items, j ) => {
                          if (item == items){
//                              console.log(1,j);
                            $this.tableData3.splice(j, 1);
                          }
                      });
                  });
                  $this.multipleSelection = [];

                }else {
                  $this.$message({
                    type: 'info',
                    message: '删除失败！'
                  });
                }
              }).catch(function (error) {
                console.log(error);
              });
//          this.$message({
//            type: 'success',
//            message: '删除成功!'
//          });
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });
            });
          }else {
            alert("未选中文件！")
          }
      },
    },
  }

</script>
<style>
  .sportManagement {
    padding: 30px;
    text-align: left;
  }
  .textLeft{
    text-align: left;
  }
  .cellName {
    display: inline-block;
    margin-left: 10px;
  }
  .tabBtn {
    padding-bottom: 30px;
  }
  .yearTime{
    text-align: left;
    margin: 30px 0;
  }
  .selectWrap{
    margin: 0 0 20px 0;
  }
  .selectWrap h4{
    display: inline-block;
    margin-right: 20px;
  }
  .gradeWrap ul {
    display: inline-block;
  }
  .gradeWrap li {
    margin: 0 5px;
    display: inline-block;
    width: 100px;
    height: 35px;
    line-height: 35px;
    border: 1px solid grey;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }
  .gradeWrap li:hover {
    background: lightblue;
    color: white;
    border: 1px solid rgba(0,0,0,0);
  }
  .gradeWrap li.active {
    background: cornflowerblue;
    color: white;
    border: 1px solid rgba(0,0,0,0);
  }
  .tableWrap {
    text-align: center;
    margin-top: 20px;
  }
  .tableWrap th {
    font-weight: 600;
  }
  .tableWrap .cell {
    text-align: center;
  }
</style>




